@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(AvenirNext-Regular, AvenirNext-Regular);
@mixin f {
  font-family: 'AvenirNext-Regular', serif; }
@include font(AvenirNext-DemiBold, AvenirNext-DemiBold);
@mixin fb {
  font-family: 'AvenirNext-DemiBold', serif; }

$cl: #2C3235;
$border: rgba($cl,.32);
$brand: #609BB7;
$green: #44B85A;
$red: #C44B49;
$grey: #787C7E;

@mixin bg-4 {
  background: rgba($cl,.04); }
@mixin bg-8 {
  background: rgba($cl,.08); }
@mixin bg-16 {
  background: rgba($cl,.16); }

@mixin lh {
  line-height: (24/15); }
@mixin bs {
  box-shadow: 0 4px 16px rgba(black,.04); }
@mixin scroll {
  &::-webkit-scrollbar-track {
    background: white; }
  &::-webkit-scrollbar {
    width: 2px;
    background: white; }
  &::-webkit-scrollbar-thumb {
    @include bg-16;
    border-radius: 2px; } }

body {
  background: $cl;
  @include fb;
  font-size: 15px;
  line-height: 1.5;
  letter-spacing: .2px;
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  font-size: 15px; }
input,
textarea,
select {
  @include f; }
button,
strong,
b {
  @include fb; }
button {
  background: none; }
a {
  text-decoration: none; }

.body {
  margin-bottom: 40px; }

.align-center {
  text-align: center; }

.center {
  max-width: 1332px;
  margin: 0 auto;
  padding: 0 50px;
  @include m {
     padding: 0 15px; }
  &_sm {
    max-width: 772px; } }

.header {
  margin-bottom: 72px;
  padding-top: 16px;
  &__center {
    display: flex; }
  &__user {
    margin-left: auto; } }

.user {
  position: relative;
  &__head {
    display: flex;
    height: 40px;
    padding: 8px 9px 8px 8px;
    background: rgba(black,.04);
    border: 1px solid rgba(black,.08);
    align-items: center;
    user-select: none;
    cursor: pointer; }
  &__preview {
    flex: 0 0 24px; }
  &__pic {
    display: block;
    max-width: 24px;
    border-radius: 2px; }
  &__name {
    margin: 0 5px 0 8px;
    font-size: 16px;
    color: white; }
  &__arrow {
    font-size: 0;
    .icon {
      font-size: 5px;
      fill: rgba(white,.64); } }
  &__drop {
    display: none;
    position: absolute;
    top: 36px;
    right: -8px;
    width: 192px;
    padding: 16px;
    background: white;
    border-radius: 4px;
    @include bs; }
  &__link {
    display: block;
    color: rgba(black,.64);
    @include lh; }
  &.visible &__arrow {
    .icon {
      transform: rotate(180deg); } }
  &.visible &__drop {
    display: block; } }

.title {
  margin-bottom: 32px;
  @include f;
  font-size: 32px;
  &_3 {
    margin-bottom: 16px;
    @include fb;
    font-size: 16px; } }

.switch {
  display: block;
  position: relative;
  margin-bottom: 16px;
  user-select: none;
  cursor: pointer;
  &__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0; }
  &__input:checked + &__wrap &__icon {
    .icon {
      opacity: 1; } }
  &__wrap {
    display: flex; }
  &__icon {
    height: 16px;
    margin: 3px 8px 0 0;
    padding: 4px 2px;
    background: rgba($brand,.16);
    border-radius: 2px;
    flex: 0 0 16px;
    transition: background .2x;
    .icon {
      display: block;
      font-size: 8px;
      opacity: 0;
      fill: $brand;
      transition: opacity .2s; } }
  &__content {
    @include f;
    @include lh;
    transition: color .2s; }
  &_panel &__wrap {
    padding: 16px;
    border-radius: 4px;
    @include bs; }
  &_radio &__icon,
  &_panel &__icon {
    padding: 4px;
    border-radius: 50%;
    .icon {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: $brand; } }
  &_panel &__input:disabled + &__wrap {
    background: rgba(black,.04);
    box-shadow: none;
    cursor: not-allowed; }
  &_panel &__input:disabled + &__wrap &__icon {
    @include bg-16; }
  &_panel &__input:disabled + &__wrap &__content {
    opacity: .64; }
  &_gray &__icon {
    @include bg-16; }
  &_gray &__content {
    color: rgba($cl,.64); }
  &_gray &__input:checked + &__wrap &__icon {
    background: rgba($brand,.16); }
  &_gray &__input:checked + &__wrap &__content {
    color: $cl; } }

.toggle {
  &__icon,
  &__title {
    display: inline-block;
    vertical-align: middle; }
  &__icon {
    position: relative;
    width: 16px;
    height: 16px;
    margin-right: 8px;
    border-radius: 2px;
    @include bg-8; }
  &__title {
    position: relative;
    top: 1px;
    @include lh; }
  .icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    opacity: .64;
    fill: black;
    &-plus {
      font-size: 8px; }
    &-minus {
      font-size: 2px; } } }

.field {
  position: relative;
  margin-bottom: 16px;
  &__wrap {
    position: relative; }
  &__arrow {
    position: absolute;
    top: 21px;
    right: 17px;
    font-size: 0;
    pointer-events: none;
    .icon {
      font-size: 7px;
      fill: rgba($cl,.64); } }
  &__arrow + &__input {
    padding-right: 30px; }
  &__input {
    width: 100%;
    height: 48px;
    padding: 0 12px;
    border-radius: 4px;
    border: 1px solid $border;
    transition: border-color .2s;
    &::placeholder {
      color: rgba($cl,.32); }
    &:hover {
      border-color: $brand; }
    &.blue {
      color: $brand; } }
  &__input:hover + &__label {
    opacity: .64; }
  &__input:focus + &__label,
  &__input.ng-touched.ng-not-empty + &__label,
  &__input.focus + &__label {
    top: -5px;
    opacity: 1;
    @include fb;
    font-size: 11px;
    text-transform: uppercase;
    color: $brand;
    letter-spacing: .8px; }
  &__label {
    position: absolute;
    top: 13px;
    left: 9px;
    max-width: 100%;
    padding: 0 4px;
    background: white;
    opacity: .32;
    @include f;
    pointer-events: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    transition: all .2s; }
  &__drop {
    display: none;
    position: absolute;
    top: 40px;
    left: -4px;
    right: 24px;
    padding: 4px;
    border-radius: 4px;
    background: white;
    @include bs;
    z-index: 50; }
  &__list {
    max-height: 112px;
    padding: 12px 0;
    overflow: auto;
    @include scroll; }
  &__option {
    padding: 0 12px;
    color: #BBBDBE;
    cursor: pointer;
    @include lh;
    @include nl {
      margin-bottom: 8px; } }
  &__query {
    color: $grey; }
  &__error {
    display: flex;
    position: absolute;
    right: 8px;
    bottom: 40px;
    padding: 4px;
    background: white;
    .icon {
      display: block;
      font-size: 8px;
      fill: $red; } }
  &__tooltip {
    position: absolute;
    bottom: 24px;
    left: 50%;
    width: 256px;
    padding: 4px;
    background: #343a3d;
    border-radius: 6px;
    font-size: 12px;
    color: white;
    line-height: 1.34;
    text-align: center;
    transform: translateX(-50%);
    z-index: 10;
    @include m {
      right: -8px;
      left: auto;
      width: 258px;
      transform: none; }
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: -8px;
      margin-left: -10px;
      @include arr(20,10,#343a3d,b);
      @include m {
        right: 6px;
        left: auto;
        margin: 0; } }
    a {
      @include fb;
      color: #B5B7B9; } }
  &__eye {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    padding: 0 16px;
    font-size: 0;
    .icon {
      font-size: 6px;
      fill: rgba($cl,.64); } }
  &__helper {
    display: flex;
    margin-top: 8px;
    @include m {
      display: block; } }
  &__status {
    display: flex;
    padding-right: 16px;
    align-items: center;
    flex: 0 0 33.33%;
    @include m {
      margin-top: 4px;
      padding: 0; }
    &.success {
      flex: 0 0 100%; } }
  &__status.active &__check,
  &__status.success &__check {
    background: rgba($green,.16);
    .icon {
      fill: $green; } }
  &__status.active &__task {
    color: $cl; }
  &__status.success &__task {
    color: $green; }
  &__check {
    height: 16px;
    margin-right: 8px;
    padding: 5px 4px;
    border-radius: 50%;
    @include bg-16;
    font-size: 0;
    flex: 0 0 16px;
    .icon {
      font-size: 6px;
      fill: white; } }
  &__task {
    @include f;
    font-size: 12px;
    color: rgba($cl,.32);
    line-height: (16/12); }
  &.error &__input {
    color: $red; } }

.btn {
  padding: 14px 23px 13px;
  background: $brand;
  border-radius: 4px;
  border: 1px solid transparent;
  font-size: 14px;
  color: white;
  letter-spacing: .8px;
  text-transform: uppercase;
  transition: background .2s, border-color .2s;
  &:not(:disabled):hover {
    background: #6ca2bc;
    border-color: #588ea8; }
  &:not(:disabled):active {
    background: #508299;
    border-color: #508299; }
  &:disabled {
    @include bg-16;
    cursor: not-allowed; }
  &_md {
    padding: 6px 16px 5px; } }
a.btn {
  display: inline-block; }

.btn-def {
  color: rgba(black,.64); }

.row {
  display: flex;
  margin: 0 -8px;
  flex-wrap: wrap;
  @include m {
    display: block; }
  &__col {
    padding: 0 8px;
    flex: 1 0 auto;
    &_half {
      flex: 0 0 50%; }
    &_md {
      flex: 0 0 33.33%; }
    &_sm {
      flex: 0 0 140px; } } }

.block {
  margin-bottom: 16px;
  padding: 32px;
  background: white;
  border-radius: 4px;
  @include bs;
  @include m {
    padding: 16px; }
  &:hover {
    box-shadow: 0 2px 8px rgba(black,.02); } }

.foot {
  margin-top: 32px;
  text-align: center;
  &__btn {
    margin: 0 8px; } }

.hotline {
  @include f;
  font-size: 12px;
  color: white;
  text-align: center;
  line-height: 1.333;
  a {
    @include fb;
    color: #B2B5B6; } }

.text {
  margin-bottom: 32px;
  @include f;
  @include lh;
  p {
    margin-bottom: 8px; }
  strong,
  b {
    @include fb; }
  a {
    @include fb;
    color: $grey; } }

.preview {
  margin-bottom: 16px;
  &__wrap {
    margin-bottom: 16px;
    font-size: 0;
    text-align: center; }
  &__pic {
    max-width: 100%; } }

.nodes {
  margin-bottom: 32px;
  &__row {
    display: flex;
    padding: 16px 0;
    border-bottom: 1px solid rgba($cl,.04);
    align-items: center;
    &:first-child {
      padding-top: 0; } }
  &__item {
    display: flex;
    margin-right: 20px;
    align-items: center;
    flex: 1 0 auto; }
  &__status {
    height: 16px;
    margin-right: 8px;
    padding: 5px 0 0 4px;
    border-radius: 50%;
    background: rgba($green,.16);
    flex: 0 0 16px;
    .icon {
      display: block;
      font-size: 6px;
      fill: $green; } }
  &__title {
    position: relative;
    top: 1px; }
  &__btn {
    margin-left: auto; } }

.node {
  @include fb;
  color: #3d6375; }

.logo {
  padding: 64px 0 32px;
  font-size: 0;
  text-align: center;
  .icon {
    font-size: 32px;
    fill: $brand; } }

.steps {
  display: flex;
  margin-bottom: 32px;
  padding-top: 4px;
  justify-content: center;
  color: white;
  text-align: center;
  @include m {
    display: block; }
  &__item {
    margin: 0 8px;
    flex: 0 0 128px;
    @include m {
      margin: 0 0 16px; } }
  &__item.current &__number {
    color: white; }
  &__item.current &__title {
    opacity: 1; }
  &__item.current &__number,
  &__item.active &__number {
    &:after {
      @include m {
        transform-origin: calc(100% - 24px) 24px;
        transform: translateY(-100%) rotate(90deg); } } }
  &__item.active &__number {
    background: rgba($green,.16);
    color: $green; }
  &__item.active:not(:last-child) &__number {
    &:after {
      display: none; } }
  &__item:nth-child(2).current &__number {
    &:after {
      width: 168px;
      @include m {
        width: 80px; } } }
  &__item:nth-child(3).current &__number,
  &__item:nth-child(3).active &__number {
    &:after {
      width: 312px;
      @include m {
        width: 136px; } } }
  &__item:nth-child(4).current &__number,
  &__item:nth-child(4).active &__number {
    &:after {
      width: 456px;
      @include m {
        width: 192px; } } }
  &__item:nth-child(5).current &__number,
  &__item:nth-child(5).active &__number {
    &:after {
      width: 600px;
      @include m {
        width: 248px; } } }
  &__number {
    position: relative;
    width: 16px;
    height: 16px;
    margin: 0 auto 8px;
    background: rgba(white,.16);
    border-radius: 50%;
    font-size: 10px;
    color: rgba(white,.32);
    line-height: 16px;
    &:after {
      content: "";
      position: absolute;
      top: -4px;
      right: -4px;
      width: 24px;
      height: 24px;
      border-radius: 24px;
      background: rgba(white,.04);
      z-index: -2; } }
  &__title {
    opacity: .32;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: .8px;
    line-height: (16/11); } }

.captcha {
  margin-bottom: 32px; }

.phone {
  margin-bottom: 32px;
  &__container {
    position: relative;
    padding: 16px;
    background: rgba(black,.02);
    border: 1px solid rgba(black,.04);
    border-radius: 4px; }
  &__wrap {
    display: flex;
    @include f; }
  &__area {
    min-width: 1px;
    height: 30px;
    font-size: 20px;
    color: $brand;
    &[contenteditable] {
      color: $cl; } }
  &__help {
    margin-left: 4px;
    padding: 8px 0 3px;
    font-size: 12px;
    color: rgba($cl,.32);
    @include m {
      display: none; } }
  &__btn {
    position: absolute;
    top: 7px;
    right: 7px;
    width: 16px;
    height: 16px;
    @include bg-8;
    border-radius: 2px;
    font-size: 0;
    .icon {
      font-size: 8px;
      fill: rgba($cl,.64); } } }

.msg {
  margin-top: 8px;
  @include f;
  font-size: 12px;
  color: $green;
  line-height: 1.34; }

.amount {
  position: relative;
  margin-bottom: 16px;
  &__input {
    width: 100%;
    height: 48px;
    padding: 0 12px;
    border-radius: 4px;
    border: 1px solid $border;
    text-align: center;
    transition: border-color .2s;
    &::placeholder {
      color: rgba($cl,.32);
      transition: color .2s; }
    &:hover {
      border-color: $brand;
      &::placeholder {
        color: rgba($cl,.64); } } }

  &__btn {
    position: absolute;
    top: 16px;
    left: 16px;
    width: 16px;
    height: 16px;
    @include bg-4;
    border-radius: 2px;
    font-size: 0;
    &:last-child {
      left: auto;
      right: 16px; }
    .icon {
      fill: rgba($cl,.64);
      &-minus {
        font-size: 2px; }
      &-plus {
        font-size: 8px; } } } }

.file {
  position: relative;
  margin-bottom: 16px;
  &__label {
    position: absolute;
    top: -5px;
    left: 9px;
    padding: 0 4px;
    background: white;
    font-size: 11px;
    text-transform: uppercase;
    color: $brand;
    letter-spacing: .8px; }
  &__wrap {
    display: flex;
    padding: 11px 8px 11px 11px;
    border: 1px solid $border;
    border-radius: 4px;
    align-items: flex-start;
    @include m {
      display: block; } }
  &__icon {
    display: flex;
    height: 44px;
    border-radius: 4px;
    @include bg-4;
    align-items: center;
    justify-content: center;
    flex: 0 0 44px;
    @include m {
      display: none; }
    .icon {
      font-size: 16px;
      fill: rgba($cl,.32); } }
  &__content {
    margin: 0 16px;
    @include f;
    @include m {
      margin: 0 0 8px; } }
  &__title {
    margin-bottom: 4px;
    @include lh; }
  &__notes {
    display: flex;
    flex-wrap: wrap;
    @include m {
      display: block; } }
  &__option {
    margin-right: 16px;
    font-size: 12px;
    color: rgba($cl,.32);
    line-height: 1.4; }
  &__btn {
    margin: 8px 0 0 auto; } }






.bold {
  @include fb; }
